.wrapper__people {
	color: #ffffff;
	display: flex;
	justify-content: space-between;
	align-items: center;
	transition: all 0.3s;
	margin-bottom: 10px;
	.about__user {
		display: flex;
		align-items: center;

		img {
			object-fit: cover;
			border-radius: 50%;
			width: 48px;
			height: 48px;
			margin-right: 10px;
		}

		h3 {

			color: #e1e3e6;
			font-weight: 400;
			font-size: 13px;
		}
	}



}