.wrapper{width:65%;background-color:var(--bgBlocksColor);border-radius:0 16px 16px 0;position:relative;display:flex;flex-direction:column;justify-content:space-between;border:1px solid var(--borderColor);border-left:none}@media(max-width: 768px){.wrapper{width:100%}}.load{position:absolute;top:50%;left:50%;transform:translate(-50%, -50%);border:3px solid #4f4e4e;border-top:3px solid var(--secondColor);border-radius:50%;width:30px;height:30px;animation:spin 1s linear infinite}@keyframes spin{0%{transform:rotate(0deg)}100%{transform:rotate(360deg)}}.wrapper__input{width:100%;background:var(--bgColor);border-radius:0 0 15px 0}.input{display:flex;align-items:center;justify-content:space-around;padding:10px}.input form{width:100%;display:flex;justify-content:space-around;align-items:center}.input input{padding:10px;color:var(--primaryColor);background:rgba(255,255,255,.08);border-radius:8px;border:none;outline:none;width:85%;transition:all .3s}.input svg{width:24px;height:24px;fill:var(--secondColor);transition:all .3s}.input svg:hover{transform:rotate(15deg) scale(1.1)}.download__file{transform:rotate(45deg);cursor:pointer}.download__file svg{transition:all .3s;width:24px;height:24px;fill:rgba(255,255,255,.3);border-radius:50%}.download__file svg:hover{transform:rotate(-45deg) scale(1.1);fill:rgba(255,255,255,.5)}.header{padding:10px 15px;display:flex;justify-content:space-between;position:relative;border-bottom:1px solid var(--borderColor)}.left__side{display:flex;align-items:center}.left__side img{border-radius:50%;object-fit:cover;width:calc(20px + 1vw);height:calc(20px + 1vw);transition:all .3s;margin-right:10px}.person__info h1{margin:0;padding:0;font-size:calc(14px + .1vw);color:#fff;font-weight:500}.person__info p{font-size:calc(10px + .1vw);margin:0;color:green}.person__info p:after{content:"";margin-left:5px}.right_side{display:flex;align-items:center}.right_side svg{transition:all .3s;width:24px;height:24px;fill:#000;border-radius:50%;border:1px solid rgba(0,0,0,0)}.right_side svg:hover{border:1px solid rgba(0,0,0,.2)}.emptity__chat{width:50%;flex-grow:1;display:flex;align-items:center;justify-content:center;border-radius:0 16px 16px 0;background-color:var(--bgBlocksColor);border:1px solid #434343;border-left:none}.emptity__content{display:flex;flex-direction:column;justify-content:center;align-items:center}.emptity__content img{width:56px;height:56px}.emptity__content span{font-size:14px;padding:5px 10px;background-color:rgba(255,255,255,.08);color:#fff;border-radius:17px;margin-top:5px}.input__file{position:absolute;width:1px;height:1px;margin:-1px;border:0;padding:0;white-space:nowrap;clip-path:inset(100%);clip:rect(0 0 0 0);overflow:hidden}.button__send{cursor:pointer}.button__send svg{transition:all .3s;fill:rgba(255,255,255,.3)}.button__send svg:hover{fill:rgba(255,255,255,.5)}.button__send:disabled{background-color:#ff0}.button__send:disabled svg{fill:red}.sceletons{margin-right:4px;padding:10px 0;overflow-y:auto;height:100%}