.dialog__overlay{
	position: fixed;
	top: 0;
	bottom: 0;
	left: 0;
	right: 0;
	z-index: 100;
	background-color: rgba(0,0,0,0.4);
}

.wrapper__search__block{
	width: 30%;
	padding: 30px 15px;
	position: fixed;
	top: 50%;
	left: 50%;
	transform: translate(-50%,-50%);
	background-color: #0f0f0f;
	border-radius: 16px;
	border:1px solid #434343;

	.wrapper__title{
		margin: 0;
		color: white;
		font-size: 14px;
		font-weight: 400;
		margin-bottom: 20px;
	}

	.list__allpeople{
		margin-top: 20px;
	}
}